import Layout from '../components/Layout';
import Button from 'react-bootstrap/Button';
const marginS = '3vw ';
function testCreateMarkup() {
	//   return {__html: '<script type="text/javascript" src="https://auth.robokassa.ru/Merchant/PaymentForm/FormSS.js?MerchantLogin=heylion&InvoiceID=0&Culture=ru&Encoding=utf-8&OutSum=500,00&isTest=1&SignatureValue=eb4d7fe36c1bf8fd946ed28d26e7cf5d"></script>'};
	return {
		__html:
			'<script type="text/javascript" src="https://auth.robokassa.ru/Merchant/PaymentForm/FormSS.js?' +
			'MerchantLogin=heylion' +
			'&InvoiceID=678678' +
			'&OutSum=500.00' +
			'&isTest=1' +
			'&SignatureValue=bfee25079fd8c072df94b59f6fdfda6a"></script>',
	};
}
const main = {
	backgroundImage:
		'linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)),url(https://sun9-50.userapi.com/c858036/v858036219/1b062a/Ime8RDzN648.jpg)',
	position: 'fixed',
	zIndex: '-2',
	top: '0',
	right: '0',
	left: '0',
	backgroundRepeat: 'no-repeat',
	backgroundSize: 'cover',
	height: '100vh',
	display: 'flex',
	flexDirection: 'column',
	justifyContent: 'space-evenly',
	color: 'white',
};
const main__heading = {
	margin: '0 1rem',
	fontWeight: 'bold',
	textAlign: 'center',
	color: 'white',
	fontSize: '1rem',
};
const main__text = {
	...main__heading,
	fontSize: '1.8rem',
	lineHeight: '1.6rem',
	padding: '0 25px',
};
const content = {
	backgroundColor: 'white',
	display: 'flex',
	flexDirection: 'column',
};
const column = {
	display: 'flex',
	flexDirection: 'column',
};
const main__content = {
	height: 'calc(100vh - 70px)',
	display: 'flex',
	flexDirection: 'column',
	justifyContent: 'space-evenly',
	backgroundColor: 'transparent',
};
const buy_button = {
	margin: '1rem auto',
};
const about_button = {
	margin: '0 auto',
};
const content__heading = {
	margin: marginS,
};
const content__picture = {
	width: '100%',
	padding: marginS,
};
const content__text = {
	margin: marginS,
	fontSize: '1.1rem',
};
const content__inner_heading = {
	marginTop: marginS,
};
const content__list_heading = {
	margin: marginS + marginS + '0 ' + marginS,
};
const payment__wrapper = {
	display: 'flex',
	flexDirection: 'column',
	padding: marginS,
	height: '500px',
	backgroundColor:'white',
	justifyContent: 'space-evenly',
	width: '100%',
};
const paymet__item_button = {
	height: '70px',
};
const paymet__item_wrapper = {
	display: 'flex',
	flexDirection: 'column',
	padding: marginS,
	height: '180px',
	margin: marginS,
	border: '10px rgb(218,112,214) solid',
	width: '100%',
};
const Study1 = () => {
	const paymentRef = React.createRef();
	const aboutRef = React.createRef();
	const scrollToRef = ref => () =>
		window.scrollTo({
			left: 0,
			top: ref ? ref.current.offsetTop : 0,
			behavior: 'smooth',
		});
	return (
		<Layout>
			<div style={main}></div>
			<div style={main__content}>
				<div style={main__text}>
					Создай набор из 3 срезов камней с помощью пошаговой инструкции и наглядного видео-примера!
				</div>
				<div style={column}>
					<h1 style={main__heading}>ГАЙД ПО СОЗДАНИЮ СРЕЗОВ КАМНЕЙ (+видео)</h1>
					<Button style={buy_button} onClick={scrollToRef(paymentRef)}>
						{' '}
						ХОЧУ НАУЧИТЬСЯ{' '}
					</Button>
					<Button variant="light" style={about_button} onClick={scrollToRef(aboutRef)}>
						подробнее
					</Button>
				</div>
			</div>
			<div style={content} ref={aboutRef}>
				<h2 style={content__heading}>
					Гайд <br /> “3 среза камня из эпоксидной смолы SKY SHINE”
				</h2>
				<div style={content__text}>
					- это подробная инструкция, как своими руками создать набор из 3 декоративных срезов,{' '}
					<b>даже если вы никогда не работали со смолой.</b>
					<br />
					<br />
					Бонус к этому гайду - <b>видео</b>, на котором показано, как создавался данный набор, а также{' '}
					<b> файл с необходимыми материалами</b> и ссылками для их приобретения.
					<br />
					<br />
					Навыки рисования вам НЕ НУЖНЫ! У вас в любом случае получится - для создания срезов достаточно в
					нужном порядке лить смолу со специальными красками и пигментами.
					<picture>
						<img
							style={content__picture}
							src="https://sun9-24.userapi.com/c857724/v857724598/1b86d8/5zRwgcCkmzw.jpg"
						/>
					</picture>
					Мастер - Калмыкова Дарья @hey_lion_art, художница-смолянист, провела уже более 25
					офлайн-мастер-классов по работе с эпоксидной смолой.
					<h2 style={content__inner_heading}>КОМУ ПОДОЙДЕТ ЭТОТ ГАЙД</h2>
					<ul style={content__text}>
						<li>
							<b>Начинающим</b> смолянистам, кто хочет научиться работать со смолой
							<br />
						</li>
						<li>
							<b>Любому</b>, кто хочет <b>создавать своими руками</b> изысканные подарки и украшать свой
							дом!
							<br />
						</li>
						<li>
							<b>Опытным художникам</b>, чтобы освоить новую технику
							<br />
						</li>
					</ul>
					<h3 style={content__inner_heading}>Кому точно не подойдет гайд</h3>
					Со смолой нельзя работать <b>беременным</b>, людям с <b>заболеваниями органов дыхания</b>, а также
					людям с <b>сильными аллергическими реакциями</b>.
				</div>
				<h2 style={content__list_heading}>СОДЕРЖАНИЕ ГАЙДА</h2>
				<ul style={content__text}>
					<li>
						<b>Подготовка к работе:</b>
						<br />
						что такое эпоксидная смола, особенности её использования, техника безопасности
					</li>
					<li>
						<b>Материалы</b> для создания 3 срезов камней
					</li>
					<li>
						<b>Начальный этап создания:</b>
						<br />
						подготовка молдов, работа с поталью, грамотное замешивание смолы, схема разлива красок
					</li>
					<li>
						<b>Создание срезов:</b> подробное описание
					</li>
					<li>
						<b>Особенности работы горелкой</b>
						<br />
						<ul style={{ listStyleType: 'disc' }}>
							<li>Файл с описанием техники безопасности и материалов</li>
							<li>Видео-процесс заливки срезов</li>
						</ul>
					</li>
				</ul>
				<h3 style={content__list_heading}>А что, если я никогда не работал(а) со смолой до этого?</h3>
				<p style={content__text}>
					Ничего страшного, <b>вы всё поймете!</b> В гайде описано всё <b>от первого до последнего шага</b>{' '}
					создания срезов: все материалы, схема разлива красок, расчет ровного количества смолы, необходимые
					условия для работы, описание процесса и видео самой заливки.
				</p>
				<h2 style={content__list_heading}>Что я получу после оплаты?</h2>
				<ul style={content__text}>
					<li>Общую информацию о работе с эпоксидной смолой </li>
					<li>
						Отдельный файл с подробной информацией материалах, где их купить и как{' '}
						<b>сэкономить без потери качества!</b>
					</li>
					<li>
						Расчет смолы для заливки одного среза и трёх, схему комбинирования красок для конкретных срезов
					</li>
					<li>Подробное пошаговое описание создания набора из 3 срезов камней</li>
					<li>
						<b>Видео-запись</b> заливки 3 срезов камней
					</li>
					<li>
						<b>Промокоды</b> на постоянную <b>10% скидку</b> на материалы для рисования в 2
						специализированных магазинах для resin art
					</li>
				</ul>
			</div>
			<div>
				<div style={payment__wrapper} ref={paymentRef}>
					<div style={paymet__item_wrapper}>
						Гайд + файл с материалами + видео-процесс <br />
						<div style={paymet__item_button}>
							<div dangerouslySetInnerHTML={testCreateMarkup()}></div>
						</div>
					</div>
					<div style={paymet__item_wrapper}>
						Гайд + файл с материалами + видео-процесс + обратная связь от преподавателя в личном чате
						<br /> 990 р
						<div style={paymet__item_button}>
							<div dangerouslySetInnerHTML={testCreateMarkup()}></div>
						</div>
					</div>
				</div>
			</div>
			<style jsx>{`
				div,
				p {
					font-weight: 100;
				}
				h2,
				h3 {
					background: rgb(218, 112, 214);
					color: white;
					padding: 25px;
					text-align: center;
				}
				b {
					font-weight: 700;
				}
			`}</style>
		</Layout>
	);
};

export default Study1;
